.UserData-content {
  width: 800px;
  min-width: 320px;
  padding: 10px;
}

.UserData-navbar {
  width: 100%;
}

.UserData-panel {
  margin-top: 40px;
  padding: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  /* justify-content: center; */
  max-height: 600px;
  overflow: hidden;
  overflow-y: auto;
}

.UserData-panel-row {
  margin: 5px;
  padding: 0px;
  /* border-radius: 5px; */
  /* border: 1px solid rgba(0, 0, 0, 0.2); */
}



.Exchange-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;

  padding: 10px;
  background-color: rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.Exchange-header-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.Exchange-header-item { 
  font-weight: bold;
}

.Exchange-content {
  padding: 5px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.Exchange-spacer {
  margin: 20px;
  -ms-flex-item-align: center;
      align-self: center;
}

.Exchange-items {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

.Exchange-value {
  font-size: 2em;
  font-weight: bold;
  -ms-flex-item-align: center;
      align-self: center;
}

.Exchange-value-deposit {
  color: green;
}

.Exchange-value-withdraw {
  color: red;
}

.VirtualItem-wrapper{
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  margin: 5px;
}

.VirtualItem-item {
  border-radius: 5px;
  padding: 5px;
  min-height: 100px;
  width: 100px;
  overflow: hidden;
}

.VirtualItem-item:hover {
  /* cursor: pointer; */
  /* background-color: rgba(0, 0, 0, 0.1); */
}

.VirtualItem-item-deposit {
  background-color: rgba(0, 255, 0, 0.1);
}

.VirtualItem-item-withdraw {
  background-color: rgba(255, 0, 0, 0.1);
}

.VirtualItem-name {
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  text-align: left;
  font-weight: bold;
  font-size: 0.9em;
}

.VirtualItem-image {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -webkit-filter: saturate(123%);
          filter: saturate(123%);
}

.VirtualItem-image img {
  width: 100%;
  height: 100%;
}

.VirtualItem-price {
  position: absolute;
  bottom: 10px;
  font-weight: bold;
  left: 0;
  right: 0;
  text-align: center;
}


