.Inventory-wrapper {
  border-radius: 5px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: calc(100vh - 6.5em);
  border: 1px solid rgba(0, 0, 0, 0.1);
  background-color: rgba(140,140,140, 0.1);
  margin-left: 10px;
  margin-right: 10px;
}

.Inventory-tools {
  border-radius: 5px;

  width: 100%;
  background-color: white;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.Inventory-body {
  overflow: hidden;
  overflow-y: auto;
  height: 100%;
}

.Inventory-items {
  padding-top: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

.Inventory-loader {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100%;
}


